:root {
    --lesson-primary-color: #ba181b;
    --lesson-secondary-color: #ffd8be;
    --exam-primary-color: darkblue;
    --exam-secondary-color: #caf0f8;
    --tutorial-primary-color: #f1c40f;
    --tutorial-secondary-color: #fff2ca;
    --lab-primary-color: #f1c40f;
    --lab-secondary-color: #fff2ca;
    --task-primary-color: #008a00;
    --task-secondary-color: #d8f3dc;
    --task-primary-color-text: white;
    --task-secondary-color-text: black;
}

/****** START styles from bootstrap ******/
$font-weight-light:           300 !default;
$font-weight-normal:          400 !default;
$font-weight-bold:            700 !default;


$border-radius:               .25rem !default;
$border-radius-lg:            .3rem !default;
$border-radius-sm:            .2rem !default;

// Badges
$badge-font-size:                   75% !default;
$badge-font-weight:                 $font-weight-bold !default;
$badge-padding-y:                   .25em !default;
$badge-padding-x:                   .4em !default;
$badge-border-radius:               $border-radius !default;

$badge-pill-padding-x:              .6em !default;
// Use a higher than normal value to ensure completely rounded edges when
// customizing padding or font-size on labels.
$badge-pill-border-radius:          10rem !default;


*, ::after, ::before {
    box-sizing: border-box;
}

.progress {
    display: -ms-flexbox;
    display: flex;
    height: 1rem;
    overflow: hidden;
    font-size: .75rem;
    background-color: #e9ecef;
    border-radius: .25rem;
}

.progress-bar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-color: #007bff;
    transition: width .6s ease;
}

.badge {
    display: inline-block;
    padding: $badge-padding-y $badge-padding-x;
    // @include font-size($badge-font-size);
    font-weight: $badge-font-weight;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    // @include border-radius($badge-border-radius);
    // @include transition($badge-transition);
  
    // @at-root a#{&} {
    //   @include hover-focus {
    //     text-decoration: none;
    //   }
    // }
  
    // // Empty badges collapse automatically
    // &:empty {
    //   display: none;
    // }
  }

th {
    text-align: left;
}


/****** END styles from bootstrap ******/


.vis-item.vis-selected {
    box-shadow: 0 0 10px black !important;
}
.vis-item {
    border-radius: 6px !important;
}

.vis-item.vis-background.red {
    background-color: rgba(255, 0, 0, 0.2);
}

.vis-item.vis-background.backgroundClass {
    background-color: rgba(191, 191, 191, 0.2);
}

.vis-item.vis-background.blue {
    background-color: rgba(0, 0, 255, 0.2);
}

.startHiddenDateClass {
    background-color: black !important;
}

.endHiddenDateClass {
    background-color: red !important;
}

