.vis-item.vis-selected {
    border-color: black;
    color: black;
    box-shadow: 0 0 10px gray;
}

/* TASK */
.vis-item.task {
    font-weight: bold;
}

.vis-item.task-notstarted {
    background-color: var(--task-primary-color);
    color: var(--task-primary-color-text);
    border: 2px solid var(--task-primary-color);
}

.vis-item.task-started,
.vis-item.task-finished {
    background-color: var(--task-secondary-color);
    color: var(--task-secondary-color-text);
    border: 2px solid var(--task-primary-color);
}

/* EXAM */
.vis-item.exam {
    color: white;
    font-weight: bold;
}

.vis-item.exam-notstarted {
    background-color: var(--exam-primary-color);
    color: white;
    border: 2px solid var(--exam-primary-color);
}

.vis-item.exam-started,
.vis-item.exam-finished {
    background-color: var(--exam-secondary-color);
    color: black;
    border: 2px solid var(--exam-primary-color);
}

/* LESSON */
.vis-item.lesson {
    font-weight: bold;
}

.vis-item.lesson-notstarted {
    background-color: var(--lesson-primary-color);
    color: white;
    border: 2px solid var(--lesson-primary-color);
}

.vis-item.lesson-started,
.vis-item.lesson-finished {
    background-color: var(--lesson-secondary-color);
    color: black;
    border: 2px solid var(--lesson-primary-color);
    border-width: 2px;
}

    .vis-item.lesson-started .fa-book-reader {
        color: var(--lesson-primary-color) !important;
    }

/* TUTORIAL */
/*.fa-book {
    font-size: 150% !important;
}*/
.vis-item.tutorial {
    font-weight: bold;
}

.vis-item.tutorial-notstarted {
    background-color: var(--tutorial-primary-color);
    color: black;
    border: 2px solid var(--tutorial-primary-color);
}

.vis-item.tutorial-started,
.vis-item.tutorial-viewed,
.vis-item.tutorial-finished {
    background-color: var(--tutorial-secondary-color);
    color: black;
    border: 2px solid var(--tutorial-primary-color);
}

.vis-item.lab {
    font-weight: bold;
}

.vis-item.lab-notstarted {
    background-color: var(--lab-primary-color);
    color: black;
    border: 2px solid var(--lab-primary-color);
}

.vis-item.lab-started,
.vis-item.lab-finished {
    background-color: var(--lab-secondary-color);
    color: black;
    border: 2px solid var(--lab-primary-color);
}


